import {lbData} from '../../../static/config/constant.js'
export default{
  data(){
    return{
      msg:'',
      lbData,   // lb数据
      option:{   // swiper的相关配置
        interval:4000,
        duration:400,
        autoplay:false
      },
      currentIdx:0,   // 当前的索引
      indicatorArr:[]   // 面板指示的个数
    }
  },
  mounted(){
    for(let i = 0;i<this.lbData.length;i++){
      this.indicatorArr.push('')
    }
  },
  methods:{
    changeSwiperHandler(e){
      this.currentIdx = e.detail.current    // 当前高亮的索引值
    }
  }
}
